@import "./_mixin.scss";

* {
  margin: 0;
  padding: 0;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

:root {
  --color-primary: #000000;
  --color-secondary: #1d2228;

  --color-accent-primary: #14c18b;
  --color-accent-secondary: #fb8122;

  --color-hight-light: #4a2eeb;
  --color-low-light: #3d4248;

  --color-font-primary: #eee;
  --color-font-secondary: #e0e0e0;

  --line: 1px solid var(--color-grey-light-2);
}

html {
  scroll-behavior: smooth;
  font-size: 62.5%; // 1rem = 10px;

  @include maxWidth(s) {
    font-size: 50%; // 1rem = 8px;
  }
  @include maxWidth(xs) {
    font-size: 43.75%; // 1rem = 7px;
  }
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  box-sizing: border-box;

  color: var(--color-font);
  font-size: 1.6rem;
  background-color: var(--color-primary);
}

::selection {
  background-color: var(--color-hight-light);
  color: var(--color-font-primary);
}

.pointer {
  cursor: pointer;
}
