@import "../../scss/_mixin.scss";

.container {
  height: 100vh;
  position: relative;
  overflow: hidden;
}

.contentWrapper {
  width: 100%;
  height: 100%;
  position: absolute;

  display: flex;
  justify-content: center;
  align-items: center;

  mask: radial-gradient(7.826rem at 50% 10.5rem, #000 99%, #0000 101%)
      calc(50% - 7rem) 0/14rem 100%,
    radial-gradient(7.826rem at 50% -7rem, #0000 99%, #000 101%) 50% 3.5rem/14rem
      100% repeat-x;
  background-color: var(--color-secondary);
}

.content {
  @include contentSize;

  &.active {
    .title {
      transition: transform 0.8s, background-position 4s ease 0.8s;
      background-position: 0%;
      transform: unset;
      @include maxWidth(s) {
        transition: transform 0.8s, background-position 3s ease 0.8s;
      }
    }

    .desc {
      transition: all 0.8s ease 0.3s;
      transform: unset;
      color: var(--color-font-secondary);
    }
  }
}

.title {
  margin-top: 5rem;
  font-size: 5rem;
  font-weight: bold;

  @include imageText(
    linear-gradient(
      120deg,
      var(--color-accent-secondary) 50%,
      var(--color-low-light) 50%
    )
  );

  background-size: 220%;
  background-position: 100%;

  transition: transform 0.4s, background-position 0.4s;
  transform: translateY(50vh);
  transform-origin: center;
}

.desc {
  transition: all 0.4s;
  transform: translateY(50vh);
  color: transparent;
}

.activeArea {
  height: 60%;
  position: absolute;
  z-index: -1;
  bottom: 0;
}
